<template>
    <div class="box" v-if="isexist">
        <draggable
        v-model="info"
  
        :options="{group:{ name:'file',  pull:true, put:true },sort:false,ghostClass: 'sortable-ghost',animation: 350,}" 
        @add="handleadd"
        :move="allowMoveAndDrop"
        @remove="handleremove">
            <transition>
            
            
            <el-card
            shadow="hover">
               <box
               :data="data.cardinfo">
               </box> 
            </el-card>
            </transition>
        </draggable>
    </div>
</template>

<script>
import draggable from 'vuedraggable'
import box from './box'

export default {
    name:'dndbox',
    components:{
        draggable,
        box
    },
    data(){
        return {
            isexist:true,
            dropnum:'',
            dragnum:'',
            checkForIsAllowDrop:true,//
        }
    },
    props:{
        data:Object,
        index:Number,

    },
    computed:{
        info:{
            get: function(){
                return [this.data]     
            },
            set: function(){

            }    
        },
        // testdrop(item){
        //     return function(item){
           
        //         return item[0].cardinfo.allowdrop
        //     }
        // }
        

    },
    methods:{
        handleadd(e){
            //找到iconbox
            if(!this.checkForIsAllowDrop)return false
            let temp=e.item.__vue__.$children[0]  //这是移动的物体的iconbox
            // console.log(temp)
            this.dropnum=e.target.parentElement.__vue__.index ;// 找到drop的父元素
           
          
            
            this.$emit('mydrop',this.dropnum)
        },
        handleremove(e){
             if(!this.checkForIsAllowDrop)return false
            this.dragnum=e.target.parentElement.__vue__.index// 找到drag的父元素
          
            this.$emit('mydrag',this.dragnum)
         
            this.isexist=false
        },
        allowMoveAndDrop(evt, originalEvent){

            let isallowdrop = evt.to.parentElement.__vue__.data.cardinfo.allowdrop //是否允许放置
     
            this.checkForIsAllowDrop=isallowdrop; //移动到不能drop的地方就把这个变为false
           
            return  isallowdrop 
        },
       
    },
    watch:{
        
    }
    
}
</script>

<style scoped>
.box{
    width:100px;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    margin: 1% 1%;
    margin-bottom: 2%;
    min-height: 50px;
    border-radius: 10px;
    position:relative;
    overflow:hidden;
}
.box>div{
    min-height:100px;
}
.sortable-ghost{
    z-index:10;
    position:absolute;
     display:none;
}
.sortable-ghost.test{
    display:none;
}
</style>


